import React from 'react';
import { Button } from '@mui/material';
import ProfileButton from './ProfileButton';
import styles from './ProfilePage.module.css';
import Food from './Components/Food';
import Vacation from './Components/Vacation';
import Music from './Components/Music';
import Job from './Components/Job';
import Hobby from './Components/Hobby';

const ButtonRow = ({ onButtonClick }) => {
  const buttons = [
    { color: 'red', label: 'Food', content: <Food /> },
    { color: 'blue', label: 'Travel', content: <Vacation /> },
    { color: 'green', label: 'Music', content: <Music /> },
    { color: 'green', label: 'Job', content: <Job /> },
    { color: 'green', label: 'Hobby', content: <Hobby /> },
    // Add more buttons as needed
  ];

  return (
    <div className={styles.buttonRowContainer}>
      <div className={styles.buttonRow}>
        {buttons.map((button, index) => (
          <ProfileButton key={index} color={button.color} label={button.label} onClick={() => onButtonClick(button.content)} />
        ))}
      </div>
    </div>
  );
};

export default ButtonRow;
