import React, { useEffect, useState } from "react";
import styles from "./TinderCards.module.css";
import TinderCard from "react-tinder-card";
import axios from "../../Services/axios";

function TinderCards({ tinderCardRef, userId }) {
  const [candidates, setCandidates] = useState([]);
  const [currentCandidateIndex, setCurrentCandidateIndex] = useState(0);
  const [currentInterestIndex, setCurrentInterestIndex] = useState(0);

  useEffect(() => {
    async function fetchCandidates() {
      try {
        const response = await axios.get(
          `http://localhost:8001/user/get-candidates/${userId}`
        );
        setCandidates(response.data);
      } catch (error) {
        console.error("Error fetching candidates:", error);
      }
    }
    if (userId) {
      fetchCandidates();
    }
  }, [userId]);

  const handleCardClick = () => {
    console.log("Card Clicked");
    setCurrentInterestIndex(
      (prevIndex) =>
        (prevIndex + 1) %
        candidates[currentCandidateIndex]?.interests.length
    );
  };

  const handleCardSwipe = () => {
    setCurrentCandidateIndex(
      (prevIndex) => (prevIndex + 1) % candidates.length
    );
    setCurrentInterestIndex(0);
    console.log("Card Swiped");
  };

  const outOfFrame = (userId) => {
    console.log("User left the screen:", userId);
    const updatedCandidates = candidates.filter(
      (candidate) => candidate.userId !== userId
    );

    setCandidates(updatedCandidates);
    handleCardSwipe();
  };

  return (
    <div className={styles.tinderCards__cardContainer}  onClick={handleCardClick}>
      {candidates.map((candidate, index) => (
        <TinderCard
          key={candidate.userId}
          ref={tinderCardRef}
          className={styles.tinderCards__swipe}
          preventSwipe={["up", "down"]}
          onSwipe={(dir) => {
            outOfFrame(candidate.userId);
          }}
         
        >
          <div
            className={styles.tinderCards__card}
            style={{
              backgroundImage: `url(${
                candidates[index]?.interests[currentInterestIndex]?.imgUrl ||
                ""
              })`,
            }}
          >
            <div className={styles.tinderCards__top__shadow}>
              <h3>{candidate.name || ""}</h3>
            </div>
            <div className={styles.tinderCards__left__shadow}>
              <h3>
                {candidate.interests[currentInterestIndex]?.title || ""}
              </h3>
              <div className={styles.tinderCards__description}>
                <p>
                  {candidate.interests[currentInterestIndex]?.description ||
                    ""}
                </p>
              </div>
            </div>
          </div>
        </TinderCard>
      ))}
    </div>
  );
}

export default TinderCards;

