import React, { useState } from 'react';
import styles from './LoginCard.module.css';
import FormButton from './Components/FormButton';
import FormHeaderComponent from './Components/FormHeaderComponent';
import FormInput from './Components/FormInput';
import FormOtherMethods from './Components/FormOtherMethods';
import axios from 'axios';

const LoginCard = ( { goToHome, setUserId}) => {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [email, setEmail] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [isSignUp, setIsSignUp] = useState(false);


  const handleLogin = async () => {
    try {
      const response = await axios.post('http://localhost:8001/user/login', {
        username,
        password,
      });

      const { userId } = response.data;
      setUserId(userId);

      console.log('Login successful:', response.data.message);
      goToHome();
    } catch (error) {
      console.error('Error logging in:', error);
    }
  };

  const handleSignUp = async () => {
    try {
      const response = await axios.post('http://localhost:8001/user/register', {
        username,
        email,
        password,
      });
  
      const { userId } = response.data;
      setUserId(userId); 
  
      console.log('User registered successfully:', response.data.message);
      goToHome();
    } catch (error) {
      console.error('Error registering:', error);
    }
  };
  

  const toggleSignUp = () => {
    setIsSignUp(!isSignUp);
  };

  return (   

    <div className={styles.loginform}>
      <FormHeaderComponent title={isSignUp ? 'Sign Up' : 'Login'} />
      <FormInput
        key="username"
        id="username"
        description="Username"
        placeholder="Enter your username"
        type="text"
        value={username}
        onChange={(e) => setUsername(e.target.value)}
      />
      {isSignUp && (
        <>
          <FormInput
            key="email"
            id="email"
            description="Email"
            placeholder="Enter your email"
            type="text"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
          />
          <FormInput
            key="password"
            id="password"
            description="Password"
            placeholder="Enter your password"
            type="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          />
          <FormInput
            key="confirmPassword"
            id="confirmPassword"
            description="Confirm Password"
            placeholder="Confirm your password"
            type="password"
            value={confirmPassword}
            onChange={(e) => setConfirmPassword(e.target.value)}
          />
          <FormButton signUp={'Sign Up'} onSignUp={handleSignUp} />
        </>
      )}
      {!isSignUp && (
        <>
          <FormInput
            key="password"
            id="password"
            description="Password"
            placeholder="Enter your password"
            type="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          />
          <FormButton signUp={'Log In'} onSignUp={handleLogin} />
          <FormButton signUp={'Sign Up'} onSignUp={toggleSignUp} />
        </>
      )}

      <FormOtherMethods />
    </div>

  );
};

export default LoginCard;
