import React, { useEffect, useState } from "react";
import {
	Card,
	CardHeader,
	CardMedia,
	CardContent,
	Typography,
	IconButton,
	TextField,
	Button,
	Dialog,
	DialogActions,
	DialogContent,
	DialogContentText,
	DialogTitle,
} from "@mui/material";
import EditIcon from "@mui/icons-material/Edit";
import CheckIcon from "@mui/icons-material/Check";
import ImageIcon from "@mui/icons-material/Image";
import styles from "./ProfilePage.module.css";
import axios from "../../Services/axios";

const ProfilePageCard = ({ userId, index }) => {
	const [interests, setInterests] = useState([]);
	const [editMode, setEditMode] = useState(false);
	const [editedTitle, setEditedTitle] = useState("");
	const [editedDescription, setEditedDescription] = useState("");
	const [editedImgUrl, setEditedImgUrl] = useState("");
	const [openImageDialog, setOpenImageDialog] = useState(false);
	const [selectedImage, setSelectedImage] = useState(null);

	useEffect(() => {
		async function fetchInterests() {
			console.log(userId);
			try {
				const response = await axios.get(
					`http://localhost:8001/user/get-interests/${userId}`
				);
				setInterests(response.data);
			} catch (error) {
				console.error("Error fetching interests:", error);
			}
		}
		if (userId) {
			fetchInterests();
		}
	}, [userId]);

	const handleEditClick = () => {
		setEditMode(true);
		setEditedTitle(interests[index]?.title);
		setEditedDescription(interests[index]?.description);
		setEditedImgUrl(interests[index]?.imgUrl);
	};

	const handleFinishEdit = async () => {
		try {
			const response = await axios.put(
				`http://localhost:8001/user/set-interest/${userId}`,
				{
					index,
					title: editedTitle,
					description: editedDescription,
					imgUrl: editedImgUrl,
				}
			);
			setInterests((prevInterests) =>
				prevInterests.map((interest, i) =>
					i === index
						? {
								...interest,
								title: editedTitle,
								description: editedDescription,
								imgUrl: editedImgUrl,
						  }
						: interest
				)
			);
			setEditMode(false);
		} catch (error) {
			console.error("Error updating interest:", error);
		}
	};

	const handleImageEditClick = () => {
		setOpenImageDialog(true);
		setSelectedImage(null);
	};

	const handleCloseImageDialog = () => {
		setOpenImageDialog(false);
	};

	const handleImageSelect = (e) => {
		const selectedFile = e.target.files[0];
		setSelectedImage(selectedFile);
	};

	const handleImageSave = () => {
		if (selectedImage) {
			const imgUrl = URL.createObjectURL(selectedImage);
			setEditedImgUrl(imgUrl);
		}
		setOpenImageDialog(false);
	};

	return (
		<div className={styles.cardContainer}>
			<Card>
      <CardHeader
    className={styles.cardHeader}
    title={
      editMode ? (
        <TextField
          fullWidth
          value={editedTitle}
          onChange={(e) => setEditedTitle(e.target.value)}
          inputProps={{ style: {  color: "white", fontSize: '1.25rem' } }}
        />
      ) : (
        <Typography variant="h6"   >
          {interests[index]?.title}
        </Typography>
      )
    }
					action={
						<IconButton onClick={editMode ? handleFinishEdit : handleEditClick}>
							{editMode ? <CheckIcon /> : <EditIcon />}
						</IconButton>
					}
				/>
				<CardContent className={styles.cardContent}>
					<div className={styles.cardDescription}>
						{editMode ? (
							<TextField
								fullWidth
								multiline
								rows={3}
								value={editedDescription}
								onChange={(e) => setEditedDescription(e.target.value)}
							/>
						) : (
							<Typography variant="body1">
								{interests[index]?.description}
							</Typography>
						)}
					</div>
					<div
						className={`${styles.cardImage} ${
							editMode && styles.editModeImage
						}`}
						onClick={handleImageEditClick}
					>
						<CardMedia
							component="img"
							alt={editedTitle}
							height="140"
							image={
								selectedImage
									? URL.createObjectURL(selectedImage)
									: editMode
									? editedImgUrl
									: interests[index]?.imgUrl
							}
							style={{ borderRadius: "7px" }}
						/>
						{editMode && (
							<div className={styles.editImageOverlay}>Edit Image</div>
						)}
					</div>
				</CardContent>
			</Card>

			<Dialog open={openImageDialog} onClose={handleCloseImageDialog}>
				<DialogTitle>Edit Image</DialogTitle>
				<DialogContent>
					<DialogContentText>
						<input type="file" accept="image/*" onChange={handleImageSelect} />
					</DialogContentText>
				</DialogContent>
				<DialogActions>
					<Button onClick={handleCloseImageDialog} color="primary">
						Cancel
					</Button>
					<Button onClick={handleImageSave} color="primary">
						Save
					</Button>
				</DialogActions>
			</Dialog>
		</div>
	);
};

export default ProfilePageCard;
